import Router from 'vue-router'
import Identity from '../components/identity/identity.vue'
import Number from '../components/number/number.vue'

/* eslint-disable*/
Vue.use(Router)

export default new Router({
  routes: [{
    path: '',
    redirect: '/identity'
  }, {
    path: '/identity',
    name: 'Identity',
    component: Identity
  }, {
    path: '/number',
    name: 'Number',
    component: Number
  }]
})



