import './myfooter.less'

export default {
  data () {
    return {}
  },
  created () {
    this.fix()
  },
  methods: {
    // 
    fix () {
      var wHeight = window.innerHeight   // 

      window.onresize = function () {
        var hh = window.innerHeight  // 
        if (wHeight > hh) {           // 
          document.querySelector('.foot-warp').style.display = 'none'
        } else {         // 
          document.querySelector('.foot-warp').style.display = 'block'
        }
        wHeight = hh
      }
    }
  },
  components: {}
}



